@import "https://fonts.googleapis.com/css2?family=Cormorant+Garamond:wght@300;400&family=Montserrat:wght@200;400;600;800&display=swap";
* {
  box-sizing: border-box;
}

html, body {
  scroll-behavior: smooth;
  height: auto;
  min-height: 100%;
  margin: 0;
  padding: 0;
  font-family: Cormorant Garamond, sans-serif;
}

.app-container {
  flex-direction: column;
  min-height: 100vh;
  display: flex;
}

.home-section {
  text-align: center;
  background-image: url("Home_pic.c11ecffc.jpg");
  background-position: center;
  background-size: cover;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100vh;
  display: flex;
  position: relative;
  overflow: hidden;
}

.home-content {
  text-align: center;
  padding: 0 1rem;
}

.home-section h1 {
  font-optical-sizing: black;
  margin: 0;
  font-family: Cormorant Garamond, serif;
  font-size: clamp(2rem, 5vw, 4rem);
}

.home-section h2 {
  font-optical-sizing: black;
  padding-top: 1rem 0;
  font-size: clamp(1rem, 3vw, 2rem);
  font-weight: 400;
}

home-arrow-img {
  width: 2rem;
  height: 2rem;
  margin-top: 2rem;
  animation: 2s infinite bounce;
}

@keyframes bounce {
  0%, 100% {
    transform: translateY(0);
  }

  50% {
    transform: translateY(-10px);
  }
}

.bounce {
  animation: 1s infinite bounce;
}

.about-section {
  background-image: url("About_pic.a6a42a0e.jpg");
  background-position: center;
  background-size: cover;
  flex-wrap: wrap;
  width: 100%;
  padding: 4rem 1rem;
  display: flex;
  position: relative;
}

.about-content {
  text-align: center;
  background-color: #ffffffe6;
  border-radius: 8px;
  max-width: 800px;
  margin: auto;
  padding: 2rem;
}

.about-content h2 {
  margin-top: 0;
  font-size: 2rem;
}

.about-content p {
  font-size: 1rem;
  line-height: 1.5;
}

.about-skills {
  columns: 2;
  text-align: left;
  column-gap: 2rem;
  max-width: 800px;
  margin: 2rem auto;
  padding-left: 1rem;
  font-size: 1.1rem;
  list-style: outside;
}

.about-skills li {
  break-inside: avoid;
}

.about-skills li::marker {
  color: #264d00;
  font-size: 1.1em;
}

.about-quote {
  margin-top: 1.5rem;
  font-style: italic;
}

@media (width <= 768px) {
  .about-content {
    width: 90%;
    padding: 1.5rem;
  }

  .about-skills {
    columns: 1;
    text-align: left;
  }
}

@media (width <= 480px) {
  .home-section h1 {
    font-size: 2rem;
  }

  .home-section h2 {
    font-size: 1.2rem;
  }

  .home-arrow-img {
    width: 1.5rem;
    height: 1.5rem;
    margin-top: 1rem;
  }
}

.main-header {
  z-index: 1000;
  backdrop-filter: blur(6px);
  background: #ffffffbf;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 1rem 5%;
  display: flex;
  position: sticky;
  top: 0;
  box-shadow: 0 2px 4px #0000001a;
}

.main-header a {
  color: #222;
  font-weight: 500;
  text-decoration: none;
  transition: color .3s;
}

.main-header a:hover {
  color: #07c;
}

.nav-logo {
  cursor: default;
  font-size: 1.5rem;
  font-weight: bold;
}

.nav-links {
  flex: 1;
  justify-content: center;
  gap: 3rem;
  display: flex;
}

.nav-links a {
  font-size: 1.5rem;
  font-weight: 500;
  text-decoration: none;
  transition: color .3s;
}

.nav-links a:hover {
  color: #007bff;
}

.nav-toggle {
  cursor: pointer;
  font-size: 1.5rem;
  display: none;
}

@media (width <= 768px) {
  .main-header {
    flex-direction: column;
    align-items: center;
    gap: 1rem;
  }
}

.background {
  object-fit: cover;
  z-index: -1;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.min-height {
  min-height: 100vh;
  position: relative;
}

@media (width <= 768px) {
  .nav-links {
    text-align: center;
    background-color: #fff;
    flex-direction: column;
    gap: 1rem;
    width: 100%;
    padding: 1rem 0;
    display: none;
    position: absolute;
    top: 60px;
    right: 1rem;
    box-shadow: 0 4px 10px #0000001a;
  }

  .nav-links.open {
    display: flex;
  }

  .nav-toggle {
    display: block;
  }
}

#portfolio.padding {
  padding: 2rem 1rem;
}

.portfolio-container {
  flex-flow: column wrap;
  justify-content: center;
  align-items: flex-start;
  gap: 2rem;
  display: flex;
}

.portfolio-image {
  flex: 300px;
  align-self: center;
  min-width: 250px;
  max-width: 500px;
  height: auto;
}

.portfolio-image img {
  object-fit: cover;
  border-radius: 10px;
  width: 100%;
  height: 100%;
}

.container {
  flex-direction: column;
  flex: 2 400px;
  gap: 1.5rem;
  min-width: 280px;
  display: flex;
}

.box {
  background-color: #f5f5f5;
  border-radius: 8px;
  padding: 1rem;
  transition: transform .3s;
  box-shadow: 0 2px 8px #0000001a;
}

.box:hover {
  transform: translateY(-4px);
}

.box h3 {
  margin: 0;
  font-size: 1.2rem;
}

.small {
  font-size: .9rem;
  line-height: 1.4;
}

@media (width >= 768px) {
  .portfolio-container {
    flex-direction: coloumn;
  }

  .portfolio-image {
    max-width: 40%;
  }

  .box {
    padding: 1rem .8rem;
  }
}

.footer-icons {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 1.5rem;
  display: flex;
}

.socialIcon {
  object-fit: contain;
  vertical-align: middle;
  gap: 2rem;
  width: 1.5rem;
  height: 1.5rem;
  transition: transform .2s ease-in-out;
  display: inline-block;
}

.socialIcon:hover {
  transform: scale(1.1);
}
